@font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    src: local('Montserrat Regular'), local('Montserrat-Regular'),
      url('../assets/fonts/Montserrat/montserrat-v14-latin-regular.woff2')
        format('woff2'),
      /* Chrome 26+, Opera 23+, Firefox 39+ */
        url('../assets/fonts/Montserrat/montserrat-v14-latin-regular.woff')
        format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }
  /* montserrat-500 - latin */
  @font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    src: local('Montserrat Medium'), local('Montserrat-Medium'),
      url('../assets/fonts/Montserrat/montserrat-v14-latin-500.woff2')
        format('woff2'),
      /* Chrome 26+, Opera 23+, Firefox 39+ */
        url('../assets/fonts/Montserrat/montserrat-v14-latin-500.woff')
        format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }
  /* montserrat-600 - latin */
  @font-face {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    src: local('Montserrat Medium'), local('Montserrat-Medium'),
      url('../assets/fonts/Montserrat/montserrat-v14-latin-600.woff2')
        format('woff2'),
      /* Chrome 26+, Opera 23+, Firefox 39+ */
        url('../assets/fonts/Montserrat/montserrat-v14-latin-600.woff')
        format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
  }
  
  /* roboto-regular - latin */
  @font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    src: url('../assets/fonts/Roboto/roboto-v20-latin-regular.eot'); /* IE9 Compat Modes */
    src: local('Roboto'), local('Roboto-Regular'),
      url('../assets/fonts/Roboto/roboto-v20-latin-regular.eot?#iefix')
        format('embedded-opentype'),
      /* IE6-IE8 */ url('../assets/fonts/Roboto/roboto-v20-latin-regular.woff2')
        format('woff2'),
      /* Super Modern Browsers */
        url('../assets/fonts/Roboto/roboto-v20-latin-regular.woff') format('woff'),
      /* Modern Browsers */
        url('../assets/fonts/Roboto/roboto-v20-latin-regular.ttf')
        format('truetype'),
      /* Safari, Android, iOS */
        url('../assets/fonts/Roboto/roboto-v20-latin-regular.svg#Roboto')
        format('svg'); /* Legacy iOS */
  }
  /* roboto-500 - latin */
  @font-face {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    src: url('../assets/fonts/Roboto/roboto-v20-latin-500.eot'); /* IE9 Compat Modes */
    src: local('Roboto Medium'), local('Roboto-Medium'),
      url('../assets/fonts/Roboto/roboto-v20-latin-500.eot?#iefix')
        format('embedded-opentype'),
      /* IE6-IE8 */ url('../assets/fonts/Roboto/roboto-v20-latin-500.woff2')
        format('woff2'),
      /* Super Modern Browsers */
        url('../assets/fonts/Roboto/roboto-v20-latin-500.woff') format('woff'),
      /* Modern Browsers */ url('../assets/fonts/Roboto/roboto-v20-latin-500.ttf')
        format('truetype'),
      /* Safari, Android, iOS */
        url('../assets/fonts/Roboto/roboto-v20-latin-500.svg#Roboto')
        format('svg'); /* Legacy iOS */
  }